<template>
  <div class="contents_container_inner">
    <!-- <div class="contents_title">
      <h3>  .</h3>
    </div>   -->
    <div class="contents_container_inner">
      <input type="file" @change="onFileSelected($event)" ref="fileInput" />
      <button v-on:click="imageSave()"></button>
    </div>




    <input style="display: none" type="file" @change="onFileSelected($event)" ref="fileInput2" />
    <div style="width:95%; margin:auto;">
        <img :src="imageSrc" v-if="imageSrc" style="width: 200px; height: 200px; object-fit: contain;"/><br>
        <button @click="$refs.fileInput2.click">Image Pick</button>
        <button v-on:click="myPlantProfileImageUpdate()"></button>
    </div>
    
  </div>
</template>
  
<script>
const axios = require("axios");
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);

export default {
  props: [], 
  components: {
  },
  data() {
    return {
      dataHealingActivity: healingActivityJson,
      healingActivity: [],
      showContentYn: false,
      currentPage: 1,
      postsPerPage: 10,

      imageSrc: null,
      imgDiaryCntnts: null,
      selectedFile: null,
    };
  },
  computed: {
    displayedPosts() {
      const startIndex = (this.currentPage - 1) * this.postsPerPage;
      const endIndex = startIndex + this.postsPerPage;
      return this.dataHealingActivity.slice(startIndex, endIndex);
    },
    totalPages() {
      return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
    }
  },
  created() {
  },
  methods: {
    myPlantProfileImageUpdate(){
      var cltvtActJson = {};
      // var cltvtActJsoArray = [];
      
      //    
      const currentDate = new Date();

      //    
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth() + 1;
      const day = currentDate.getDate();
      const hours = currentDate.getHours();
      const minutes = currentDate.getMinutes();
      const seconds = currentDate.getSeconds();

      //    
      const formattedDate = `${year}${String(month).padStart(2, '0')}${String(day).padStart(2, '0')}_${String(hours).padStart(2, '0')}${String(minutes).padStart(2, '0')}${String(seconds).padStart(2, '0')}`;




      // 1.    
      cltvtActJson = {
          // cltvtId: this.cltvtId,
          postRegDttm: formattedDate,
          postRegTitle: 'healing',
          postRegContent: '.',
      };

      // 2.   
      const formData = new FormData();
      formData.append("profileYn", "Y");
      formData.append("image", this.selectedFile);
      formData.append("cltvtActJson", JSON.stringify(cltvtActJson));
      
      axios
          .post("/api/file/uploadImage", formData)
          .then((response) => {
              console.log(response.status);
              alert('   .');
              this.modalClose(true);
          })
          .catch((err) => {
              console.log("uploadImage post error");
              console.log(err);
          });

          // window.location.href = "/menu2_3";
  },
    onFileSelected: function (e) {
      // 1.   .
      this.selectedFile = e.target.files[0];
      ///// validation (?  ? )

      // 2.  base64  
      var reader = new FileReader();
      reader.readAsDataURL(this.selectedFile);

      // 3.    
      reader.onload = () => {
          // console.log(reader.result);   // base64 object
          this.imageSrc = reader.result;
      };
      // this.$emit('input', this.selectedFile)
      console.log('selectedFile >>>>>>>> ', this.selectedFile);
    },
    imageSave(){
      // 2.   
      const formData = new FormData();
      formData.append("image", this.selectedFile);
      console.log('selectedFile >>>>>>>> ', this.selectedFile);
      
      axios
            .post("/api/file", {formData: formData})
            // axios.post(this.router, resultJsonImp)
            .then((response) => {  
              console.log('extractSqlResult response : ', response);
            })
            .catch((error) => {
              console.log(error);
            });

          // window.location.href = "/menu2_3";
  },
    showContent(healingActivity) {
      this.healingActivity = healingActivity;
      this.showContentYn = true;
    },
  },
};
</script>